.btStart {
  display:flex;
  justify-content: center;
  align-items: center;
	cursor: pointer;
	position: relative;
	padding: 1.0rem 2.0rem;
	border-radius: 1.5rem;
	line-height: 2.5rem;
	font-size: 2.0rem;
  font-weight: 700;
  font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
	color: rgb(90, 33, 0);
	border: 1px solid #996515;
  background-image: linear-gradient(-180deg, #FFD700 0%, #FF8C00 100%);
  
	box-shadow: 0 1rem 1.25rem 0 rgba(160, 82, 45, 0.5),
							0 -0.25rem 1.5rem rgba(255, 140, 0, 1) inset,
							0 0.75rem 0.5rem rgba(255,255,255, 0.4) inset,
							0 0.25rem 0.5rem 0 rgba(255, 215, 0, 1) inset;
}

.btStart span {
	color: transparent;
	background-image: linear-gradient(0deg, #EE82DA 0%, #FEFAFD 100%);
	-webkit-background-clip: text;
	background-clip: text;
	filter: drop-shadow(0 2px 2px hsla(290, 100%, 20%, 1));
}

.btStart::before {
	content: "";
	display: block;
	height: 0.25rem;
	position: absolute;
	top: 0.5rem;
	left: 50%;
	transform: translateX(-50%);
	width: calc(100% - 7.5rem);
	background: #fff;
	border-radius: 100%;
	
	opacity: 0.7;
	background-image: linear-gradient(-270deg, rgba(255,255,255,0.00) 0%, #FFFFFF 20%, #FFFFFF 80%, rgba(255,255,255,0.00) 100%);
}

.btStart::after {
	content: "";
	display: block;
	height: 0.25rem;
	position: absolute;
	bottom: 0.75rem;
	left: 50%;
	transform: translateX(-50%);
	width: calc(100% - 7.5rem);
	background: #fff;
	border-radius: 100%;
	
	filter: blur(1px);
	opacity: 0.05;
	background-image: linear-gradient(-270deg, rgba(255,255,255,0.00) 0%, #FFFFFF 20%, #FFFFFF 80%, rgba(255,255,255,0.00) 100%);
}

.btStart img {
  width:80px;
  margin-right: 15px;
}